import React from "react";
import {
  Table,
  Button,
  Card,
  Form,
  InputGroup,
  FormControl,
  Row,
  Col,
  DropdownButton,
  Dropdown,
} from "react-bootstrap";

import { default as t } from "../../../../translations";

export const PendingPickup = () => {
  return (
    <div>
      <Row>
        <Col>
          <h2>{t("pendingShipments")}</h2>
        </Col>
        <Col className="d-flex flex-row-reverse mx-5">
          <Button size="lg">{t("export")}</Button>
        </Col>
      </Row>
      <Row className="mt-1">
        <Form>
          <Card>
            <Card.Body>
              <Row>
                <Col md={6}>
                  <Form.Label>
                    <h2>{t("warehouse")}</h2>
                  </Form.Label>
                  <Form.Select>
                    <option selected="true" disabled="disabled">
                      --{t("selectWarehouse")}--
                    </option>
                    <option>item 1</option>
                  </Form.Select>
                </Col>
              </Row>
            </Card.Body>
          </Card>
        </Form>
      </Row>
      <Card elevation={6} className="w-100 overflow-auto mt-4">
        <Table style={{ minWidth: 750 }}>
          <thead>
            <tr>
              <th className="pl-sm-24">#</th>
              <th className="pl-sm-24">{t("awb")}</th>
              <th className="px-0">{t("pickupType")}</th>
              <th className="px-0">{t("location")}</th>
              <th className="px-0">{t("creationDate")}</th>
              <th className="px-0">{t("shipper")}</th>
              <th className="px-0">{t("shipmentType")}</th>
              <th className="px-0">{t("action")}</th>
            </tr>
          </thead>
          <tbody>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tbody>
        </Table>
      </Card>
    </div>
  );
};
export default PendingPickup;
